.text-overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.close-Icon {
  color: #e8e8e8;
  font-size: 12px;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
  cursor: pointer;
}
.close-Icon:hover {
  color: rgba(0, 0, 0, 0.335);
}
.RA-siteDetail {
  width: 100%;
  height: 60px;
  line-height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  box-shadow: 0 1px 9px -3px rgba(0, 0, 0, 0.2);
  cursor: pointer;
}
.RA-siteDetail.RA-siteDetail-horizontal {
  justify-content: unset;
  min-width: 160px !important;
}
.RA-siteDetail.RA-siteDetail-inlineLayout {
  width: auto;
  min-width: 255px;
  box-shadow: none;
}
.RA-siteDetail.RA-siteDetail-darkTheme {
  box-shadow: 0 1px 9px -3px #000000;
}
.RA-siteDetail.RA-siteDetail-collapsed .RA-siteDetail-title {
  display: none;
}
.RA-siteDetail .RA-siteDetail-logo {
  height: 45px;
}
.RA-siteDetail .RA-siteDetail-title {
  line-height: 60px;
  font-size: 18px;
  font-weight: 700;
  color: var(--primary);
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  margin-left: 8px;
}
